import * as React from 'react';
import Board from 'react-trello';
import { Card } from './card';
import { LaneHeader } from './LaneHeader';
import { CustomAddCardLink } from './CustomAddCardLink';
import { AppContext } from '../../contexts/appContext';
import { useContext } from 'react';
import * as jsoncompare from 'js-object-compare';
export function BoardView() {
  const {
    dispatchGetBoardDetails,
    boards,
    dispatchUpdateBoardDetails,
  } = useContext(AppContext);
  const [boardData, setBoardData] = React.useState({});
  //   TODO:// add dynamic board selection
  const data = Object.keys(boards).length ? boards[0].data : { lanes: [] };
  if(Object.keys(boards).length){

  
  //console.log();
}
  const translate = {
    'Add another lane': '+ Add new stack',
    'Click to add card': '+',
  };
  const customTitles = text => {
    return translate[text];
  };
  const onDataChange = newData => {
    if (!jsoncompare.sameContent(newData, JSON.parse(boards[0].orignaldata))) {
        //console.log("Not same");
        dispatchUpdateBoardDetails({
          boardId: boards[0].id,
          newBoardData: newData,
          oldBoardData: boards[0].orignaldata,
        });
      }

  };
  React.useEffect(() => {
    dispatchGetBoardDetails();
  },              []);

  return (
    <div>
      {Object.keys(boards).length ? (
        <Board
          editable
          canAddLanes
          draggable
          collapsibleLanes
          data={data}
          className="boardContainer"
          laneStyle={{ backgroundColor: '#f5f5f5', borderRadius: '5px' }}
          components={{
            Card,
            LaneHeader,
            AddCardLink: CustomAddCardLink,
          }}
          t={customTitles}
          // onLaneAdd={onLaneAdd}
          onDataChange={onDataChange}
        />
      ) : (
        ''
      )}
    </div>
  );
}
